import React,{useEffect,useState} from 'react';
import anime from 'animejs';

const handlealtura=()=>{
    let middlescreen = (window.screen.height)/2
    return window.scrollY+ middlescreen;
  }
const handlealturacomponent=(element)=>{
    try{
       let altura = document.querySelectorAll(element);
       let arr = [];
       altura.forEach((el)=>{  
            arr=[...arr,{pos:el.offsetTop,ref:el}];
       })
       return arr;
       
    }catch(e){
        return"no se recibió la clase "+e
    }  
  }
export const Riderxyturn = (...element)=>{

    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
            arr[el].ref.style.transition="all .5s ease-in";
            if(arr[el].pos<handlealtura()){
                try{
                    arr[el].ref.style.opacity="1";
                    console.log(arr[el].ref.style);
             }catch(e){
                 console.warn("a ocurrido un error "+e);
             }
            }else{
                arr[el].ref.style.opacity="0";
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}

